.ContainerWrap{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-row-gap: 2.5rem;
    grid-column-gap: 2rem;
    background: black;
    padding: 0 2vw;
}

.ContainerItem:first-child{
    grid-column-start: 1;
    grid-column-end: 1;
    grid-row-start: 1;
    grid-row-end: 3;
}

.ContainerItem{
    border: .4vw solid white;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
    flex-direction: column;
}
.SeasonTitle{
    width: 100%;
    height: 4vw;
    background-color: white;
    color: black;
    font-family: 'Cabin', sans-serif;
    text-align: center;
    font-weight: bold;
    font-size: 3vw;
}
.SeasonDesc{
    color: white;
    padding: 2rem;
    font-size: 2.5vw;
    font-weight: 700;
    font-family: 'Cabin', sans-serif;
}
.ContainerItem:first-child, .SeasonDesc {
    font-size: 2.5vw;
}

@media (max-width: 768px) {
    .ContainerWrap{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .ContainerItem{
        width: 75vw;
        text-align: center;
    }
    .SeasonTitle{
        font-size: 7vw;
        height: 9vw;
    }
    .SeasonDesc{
        font-size: 5vw;

    }
}