.ContainerWrap{
    display: flex;
    flex-direction: column;
}

.PDFFile > img,
.DownloadFile > img{
    width: 48px;
    height: 48px;
}

.PDFFile,
.DownloadFile{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    margin: 20px 0
}

.ContainerItem > a{
    text-decoration: none;
}
.File{
    font-size: 24px;
    font-family: 'Cabin', sans-serif;
    color: white;
    margin-left: 20px;
}

@media (max-width: 768px) {
    .ContainerItem{
        display: flex;
        justify-content: center;
    }
}