.SponsorsSection{
    display: flex;
    flex-display: row;
    justify-content: center;
    align-items: center;
    background: black;
    padding: 100px 0;
}
.Sponsor{
    height: 200px;
}
.SponsorImage{
    margin: 0 50px;
}
@Media (max-width: 768px){
    .SponsorsSection{
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    .Sponsor{
        height: 30vw;
    }
}