.RulesSection{
    display: flex;
    background: black;
}

.RulesTitle{
    color: white;
    font-family: 'Cabin', sans-serif;
    font-size: 60px;
    text-align: center;
    padding: 100px 0;
}

.RulesFlex{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 97vw;
}

.File{
    display: flex;
    flex-direction: column;
}
@Media (max-width: 768px){
    .RulesTitle{
        font-size: 8vw;
    }
    .RulesFlex{
        margin-top: 5vw;
        justify-content: center;
    }
    .RulesPhoto > img {
        display: none;
    }
}